@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}

body {
  background-color: darkgray;
}
.app {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
  color: aliceblue;
}

.app__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  color: white;
}

.app__stats {
  display: flex;
  justify-content: space-between;
}

.app__left {
  flex: 0.9 1;
}

.app__right {
  display: flex;
  flex-direction: column;
}

.app__graphTitle {
  margin-top: 20px;
  margin-bottom: 20px;
}

.app__right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.app__graph {
  flex-grow: 1;
}

@media (max-width: 990px) {
  .app {
    flex-direction: column;
  }
}
.backGroundImg{
  background-image: url(/static/media/covid2.dee88fce.png);
  background-color: black;
  background-repeat: no-repeat, repeat;
  height: 370px;
  background-position: center;
  background-size: cover; 
}
.formstyle{
  margin-top: 250px;
}
.nav-link{
  color: white;
  font-size: 20px;
}
.hover :hover{
  background-color: #343a40;
  color: white;
}
.nav-link{
  font-size: 24px;
}

.backGroundImg2{
  background-image: url(/static/media/banner.53d0a0a2.jpg);
  background-color: black;
  background-repeat: no-repeat, repeat;
  height: 300px;
  background-position: center;
  background-size: cover; 
}
.bContain{
  width: 60%;
}
.contain{
  height: 100%;
}





.infoBox {
  flex: 1 1;
  cursor: pointer;
}
.infoBox:not(:last-child) {
  margin-right: 12px;
}

.infoBox__cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 2.05rem;
  margin-bottom: 0.5rem;
}

.infoBox--selected {
  border-top: 20px solid greenyellow;
  border-bottom: 20px solid greenyellow;
  border-right: 20px solid greenyellow;
  border-left: 20px solid greenyellow;
}

.infoBox--red {
  border-color: red;
}

.infoBox__cases--green {
  color: lightgreen;
}

.infoBox--grey {
  border-color: grey;
}

.infoBox__cases--grey {
  color: grey;
}

.infoBox__total {
  color: black !important;
  font-weight: 700 !important;
  font-size: 0.9rem !important;
  margin-top: 15px !important;
}

.map {
  height:500px;
  background-color: white;
  padding: 1rem;
  border-radius: 20px;
  margin-top: 16px;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.info-flag img {
  width: 100px;
  border-radius: 5px;
}

.info-flag {
  height: 80px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
}

.info-confirmed,
.info-deaths,
.info-recovered {
  font-size: 16px;
  margin-top: 5px;
}

.info-name {
  font-size: 20px;
  font-weight: bold;
  color: #555;
}

.table {
  margin-top: 40px;
  overflow: scroll;
  height: 630px;
}


.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}


body, html, #root, .app, .container {
    width: 100%;
    height: 100%;
    background-color: #6c757d
  }
  
  
  .containerr {
    font-size: 28px;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
  }
  
  .loginForm {
   box-sizing: border-box;
   width: 100%;
   max-width: 400px;
   padding-left: 20px;
   padding-right: 20px;
   /* padding-top: 0em; */
  }
  
  .inputField {
   padding-top: 16px;
  }
  
  .input {
   box-sizing: border-box;
   outline: none;
   border: solid 2px #1189de;
   border-radius: 4px;
   color: #292929;
   width: 100%;
   padding: 12px;
   font-size: 14px;
   background: rgba(255, 255, 255, 1);
  }
  
  .submitButton {
   padding-top: 16px;
  }
  
  
  .button {
   width: 100%;
   min-width: 280px;
   color: #565656;
   padding: 12px;
   font-size: 14px;
   font-weight: bold;
   border: solid 2px #1189de;
   border-radius: 4px;
   background: #fff;
   cursor: pointer;
  }

@media (min-width:900px){
       .loginForm {
        box-sizing: border-box;
        width: 100%;
        min-width: 700px;
        padding-left: 20px;
        padding-right: 20px;
        /* padding-top: 0em; */
       }

}
